<template lang="pug">

    div.flex-container.flex-centered.full-height.full-width
        ISSDriverModelSVG.flex-item-row
        
</template>

<script>
import ISSDriverModelSVG from "../assets/images_src/ISS_driverModel.svg";
import { TimelineMax, TweenMax } from "gsap";

export default {
  components: {
    ISSDriverModelSVG
  },
  props: {
    step: {
      type: Number,
      default: 1
    }
  },
  watch: {
    step: function(val) {
      switch (val) {
        case 2:
          this.displaySVG();
          this.$options.timeline.play("start");
          break;
        default:
          this.$options.timeline.eventCallback(
            "onReverseComplete",
            this.hideSVG
          );
          this.$options.timeline.reverse();
          break;
      }
    }
  },
  mounted() {
    const tl = new TimelineMax();
    var connectorLength = [];
    var connectorPaths = document.getElementsByClassName("connector");

    /* Set initial state of elements */
    /* Groups */
    tl.set(["#driverAnalyses", "#driverModel", "#ISS"], {
      scaleX: 0,
      scaleY: 0,
      transformOrigin: "center"
    });
    tl.set(".cog", { scaleX: 0, scaleY: 0, transformOrigin: "center" });
    /* Connectors */
    for (let index = 0; index < connectorPaths.length; index++) {
      connectorLength[index] = connectorPaths[index].getTotalLength();
      tl.set(connectorPaths[index], {
        strokeDasharray: connectorLength[index],
        strokeDashoffset: connectorLength[index],
        autoRound: false
      });
    }
    /* Set animations */

    tl.to(
      ["#driverAnalyses"],
      0.5,
      {
        scaleX: 1,
        scaleY: 1
      },
      "start"
    );

    tl.to(connectorPaths[0], 0.5, {
      strokeDashoffset: 0
    });
    tl.to("#driverModel", 0.5, {
      scaleX: 1,
      scaleY: 1,
      ease: Back.easeOut.config(1.7)
    });
    tl.set("#fulldriver", { alpha: 0 });
    tl.to("#driverRight", 0.5, {
      rotation: 15,
      transformOrigin: "center bottom",
      ease: Back.easeOut.config(1.7)
    });
    tl.staggerFromTo(
      ".cog",
      0.25,
      { y: 5 },
      { y: 0, scaleX: 1, scaleY: 1, ease: Back.easeOut.config(1.7) },
      0.1
    );
    tl.to(".cog", 1, {
      rotation: (index, target) => {
        if (index % 2) {
          return 360;
        } else {
          return -360;
        }
      }
    });
    tl.to(
      connectorPaths[1],
      0.5,
      {
        strokeDashoffset: 0
      },
      "-=1"
    );
    tl.to(
      "#ISS",
      0.5,
      {
        scaleX: 1,
        scaleY: 1,
        ease: Back.easeOut.config(1.7)
      },
      "-=.5"
    );
    this.$options.timeline = tl;
    this.$options.timeline.progress(1);
    this.$options.timeline.progress(0);
    this.$options.timeline.pause();
    this.hideSVG();
  },
  methods: {
    displaySVG: function() {
      TweenMax.set("#ISSDriverModel", { alpha: 1 });
    },
    hideSVG: function() {
      TweenMax.set("#ISSDriverModel", { alpha: 0 });
    }
  }
};
</script>

<style scoped></style>
